"use client"

import { useState, useEffect, useRef } from "react"
import Link from "next/link"
import { useRouter, useParams } from "next/navigation"
import { Header } from "@/components/layout/header"
import { mockCompetitions } from "@/lib/mock-competitions"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group"
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover"
import { Calendar } from "@/components/ui/calendar"
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from "@/components/ui/alert-dialog"
import { format } from "date-fns"
import { ru } from "date-fns/locale"
import { ArrowLeft, Calendar as CalendarIcon, X, Trash2, Search, Check, ChevronDown } from "lucide-react"
import type { Competition } from "@/types/competition"
import { cn } from "@/lib/utils"
import { useToast } from "@/hooks/use-toast"
import { Toaster } from "@/components/ui/toaster"

// Парсинг даты из формата "DD.MM.YYYY"
const parseDate = (dateStr: string): Date | undefined => {
  if (!dateStr) return undefined
  const parts = dateStr.split('.')
  if (parts.length === 3) {
    const day = parseInt(parts[0], 10)
    const month = parseInt(parts[1], 10) - 1
    const year = parseInt(parts[2], 10)
    return new Date(year, month, day)
  }
  return undefined
}

// Форматирование даты в формат "DD.MM.YYYY"
const formatDate = (date: Date | undefined): string => {
  if (!date) return ""
  return format(date, "dd.MM.yyyy", { locale: ru })
}

export default function CompetitionEditPage() {
  const params = useParams()
  const router = useRouter()
  const { toast } = useToast()
  const competitionId = params.id as string
  const competition = mockCompetitions.find((comp) => comp.id === competitionId)

  const [isSaving, setIsSaving] = useState(false)
  const [dateRangeOpen, setDateRangeOpen] = useState(false)
  const [isScrolled, setIsScrolled] = useState(false)
  const containerRef = useRef<HTMLDivElement>(null)
  const [coOrganizer, setCoOrganizer] = useState<{ name: string; ogrn: string; inn: string } | null>(null)
  
  // Состояния для поиска в выпадающих списках
  const [sportTypeOpen, setSportTypeOpen] = useState(false)
  const [sportTypeSearch, setSportTypeSearch] = useState("")
  const [disciplineOpen, setDisciplineOpen] = useState(false)
  const [disciplineSearch, setDisciplineSearch] = useState("")
  const sportTypeSearchInputRef = useRef<HTMLInputElement>(null)
  const disciplineSearchInputRef = useRef<HTMLInputElement>(null)

  // Список видов спорта
  const sportTypes = [
    { value: "athletics", label: "Легкая атлетика" },
    { value: "football", label: "Футбол" },
    { value: "basketball", label: "Баскетбол" },
    { value: "swimming", label: "Плавание" },
    { value: "tennis", label: "Теннис" },
    { value: "volleyball", label: "Волейбол" },
    { value: "hockey", label: "Хоккей" },
    { value: "boxing", label: "Бокс" },
    { value: "wrestling", label: "Борьба" },
    { value: "gymnastics", label: "Гимнастика" },
    { value: "cycling", label: "Велоспорт" },
    { value: "skiing", label: "Лыжный спорт" },
    { value: "figure-skating", label: "Фигурное катание" },
    { value: "water-polo", label: "Водное поло" },
    { value: "handball", label: "Гандбол" },
  ]

  // Виды спорта и их дисциплины
  const sportDisciplines: Record<string, { value: string; label: string }[]> = {
    athletics: [
      { value: "100m", label: "Бег на 100 метров" },
      { value: "200m", label: "Бег на 200 метров" },
      { value: "400m", label: "Бег на 400 метров" },
      { value: "marathon", label: "Марафон" },
    ],
    football: [
      { value: "football", label: "Футбол" },
    ],
    basketball: [
      { value: "basketball", label: "Баскетбол" },
    ],
    swimming: [
      { value: "swimming-100m", label: "Плавание 100м" },
      { value: "swimming-200m", label: "Плавание 200м" },
    ],
  }

  // Автофокус на поле поиска при открытии
  useEffect(() => {
    if (sportTypeOpen && sportTypeSearchInputRef.current) {
      setTimeout(() => sportTypeSearchInputRef.current?.focus(), 100)
    }
  }, [sportTypeOpen])

  useEffect(() => {
    const handleScroll = () => {
      const scrollTop = window.scrollY || document.documentElement.scrollTop
      setIsScrolled(scrollTop > 0)
    }

    window.addEventListener('scroll', handleScroll, { passive: true })
    return () => window.removeEventListener('scroll', handleScroll)
  }, [])

  // Parse dates
  const startDate = competition?.startDate ? parseDate(competition.startDate) : undefined
  const endDate = competition?.endDate ? parseDate(competition.endDate) : undefined
  
  // Исходное состояние для отслеживания изменений
  const initialDateRange = { from: startDate, to: endDate }
  const initialFormData = {
    fullName: competition?.name || "",
    shortName: competition?.name || "",
    organizerName: competition?.organizer || "",
    ogrn: "",
    inn: "",
    sportType: "",
    discipline: "",
    participantsCount: competition?.participants?.length?.toString() || "",
    inMinistryList: competition?.inMinistryList ? "yes" : "no",
    participantGender: "both",
  }
  const initialCoOrganizer = null

  const [dateRange, setDateRange] = useState<{ from?: Date; to?: Date }>(initialDateRange)
  const [showUnsavedDialog, setShowUnsavedDialog] = useState(false)
  const [pendingNavigation, setPendingNavigation] = useState<(() => void) | null>(null)

  // Form state
  const [formData, setFormData] = useState(initialFormData)

  // Функция проверки наличия изменений (используется в разных местах)
  const checkUnsavedChanges = () => {
    // Проверка formData
    const formChanged = JSON.stringify(formData) !== JSON.stringify(initialFormData)
    
    // Проверка dateRange
    const dateChanged = 
      (dateRange.from?.getTime() !== initialDateRange.from?.getTime()) ||
      (dateRange.to?.getTime() !== initialDateRange.to?.getTime())
    
    // Проверка coOrganizer
    const coOrganizerChanged = coOrganizer !== initialCoOrganizer
    
    return formChanged || dateChanged || coOrganizerChanged
  }

  const handleSportTypeChange = (value: string) => {
    setFormData((prev) => {
      const disciplines = sportDisciplines[value] || []
      // Если только одна дисциплина, подставляем её автоматически
      const newDiscipline = disciplines.length === 1 ? disciplines[0].value : ""
      return { ...prev, sportType: value, discipline: newDiscipline }
    })
  }

  // Автофокус на поле поиска дисциплин при открытии (только если >= 6 дисциплин)
  useEffect(() => {
    if (disciplineOpen && disciplineSearchInputRef.current && formData.sportType) {
      const disciplines = sportDisciplines[formData.sportType] || []
      const shouldShowSearch = disciplines.length >= 6
      if (shouldShowSearch) {
        setTimeout(() => disciplineSearchInputRef.current?.focus(), 100)
      }
    }
  }, [disciplineOpen, formData.sportType])

  const handleAddCoOrganizer = () => {
    setCoOrganizer({ name: "", ogrn: "", inn: "" })
  }

  const handleRemoveCoOrganizer = () => {
    setCoOrganizer(null)
  }

  const updateCoOrganizer = (field: string, value: string) => {
    if (coOrganizer) {
      setCoOrganizer({ ...coOrganizer, [field]: value })
    }
  }

  if (!competition) {
    return (
      <div className="min-h-screen bg-gray-50">
        <Header />
        <div className="max-w-4xl mx-auto px-6 py-12">
          <div className="text-center">
            <h1 className="text-2xl font-semibold mb-2">Соревнование не найдено</h1>
            <p className="text-gray-600 mb-6">Соревнование с указанным ID не существует.</p>
            <button
              onClick={() => router.push("/competitions")}
              className="text-blue-600 hover:text-blue-700 font-medium"
            >
              Вернуться к соревнованиям
            </button>
          </div>
        </div>
      </div>
    )
  }

  const handleCancel = () => {
    if (checkUnsavedChanges()) {
      setPendingNavigation(() => () => router.push(`/competitions/${competitionId}`))
      setShowUnsavedDialog(true)
    } else {
      router.push(`/competitions/${competitionId}`)
    }
  }

  const handleConfirmNavigation = () => {
    setShowUnsavedDialog(false)
    if (pendingNavigation) {
      pendingNavigation()
      setPendingNavigation(null)
    }
  }

  const handleCancelNavigation = () => {
    setShowUnsavedDialog(false)
    setPendingNavigation(null)
  }

  const handleSave = async () => {
    setIsSaving(true)
    try {
      // TODO: Save competition data
      await new Promise((resolve) => setTimeout(resolve, 500))
      
      // Имитация случайной ошибки (10% вероятность для тестирования)
      const shouldFail = Math.random() < 0.1
      if (shouldFail) {
        throw new Error("Ошибка сохранения")
      }
      
      toast({
        variant: "success",
        title: "Успешно сохранено",
        description: "Данные соревнования успешно сохранены",
      })
      
      // Небольшая задержка перед переходом, чтобы пользователь увидел уведомление
      setTimeout(() => {
        router.push(`/competitions/${competitionId}`)
      }, 500)
    } catch (error) {
      setIsSaving(false)
      toast({
        variant: "destructive",
        title: "Ошибка сохранения",
        description: "Что-то пошло не так. Попробуйте сохранить еще раз.",
      })
    }
  }

  const updateField = (field: string, value: string) => {
    setFormData((prev) => ({ ...prev, [field]: value }))
  }

  // Обработка закрытия страницы/вкладки
  useEffect(() => {
    const handleBeforeUnload = (e: BeforeUnloadEvent) => {
      // Создаем снимок текущего состояния
      const formChanged = JSON.stringify(formData) !== JSON.stringify(initialFormData)
      const dateChanged = 
        (dateRange.from?.getTime() !== initialDateRange.from?.getTime()) ||
        (dateRange.to?.getTime() !== initialDateRange.to?.getTime())
      const coOrganizerChanged = coOrganizer !== initialCoOrganizer
      
      if (formChanged || dateChanged || coOrganizerChanged) {
        e.preventDefault()
        e.returnValue = ''
      }
    }

    window.addEventListener('beforeunload', handleBeforeUnload)
    return () => window.removeEventListener('beforeunload', handleBeforeUnload)
  }, [formData, dateRange, coOrganizer])

  // Перехват кликов на всех ссылках для проверки несохраненных изменений
  useEffect(() => {
    const handleDocumentClick = (e: MouseEvent) => {
      const target = e.target as HTMLElement
      const link = target.closest('a[href]') as HTMLAnchorElement | null
      
      if (!link || !link.href) return
      
      // Проверяем изменения
      const formChanged = JSON.stringify(formData) !== JSON.stringify(initialFormData)
      const dateChanged = 
        (dateRange.from?.getTime() !== initialDateRange.from?.getTime()) ||
        (dateRange.to?.getTime() !== initialDateRange.to?.getTime())
      const coOrganizerChanged = coOrganizer !== initialCoOrganizer
      const hasChanges = formChanged || dateChanged || coOrganizerChanged
      
      if (!hasChanges) return
      
      // Игнорируем ссылки внутри AlertDialog
      if (link.closest('[data-slot="alert-dialog"]')) {
        return
      }
      
      // Игнорируем ссылки, которые уже имеют onClick обработчик (обработанные вручную)
      if ((link as any).__hasCustomClickHandler) {
        return
      }
      
      // Игнорируем внешние ссылки
      try {
        const linkUrl = new URL(link.href)
        const currentUrl = window.location
        if (linkUrl.origin !== currentUrl.origin) {
          return
        }
        
        // Игнорируем текущую страницу
        if (linkUrl.pathname === currentUrl.pathname) {
          return
        }
        
        e.preventDefault()
        e.stopPropagation()
        
        setPendingNavigation(() => () => router.push(linkUrl.pathname))
        setShowUnsavedDialog(true)
      } catch (err) {
        // Если не удалось распарсить URL, пропускаем
        return
      }
    }

    document.addEventListener('click', handleDocumentClick, true)
    return () => document.removeEventListener('click', handleDocumentClick, true)
  }, [formData, dateRange, coOrganizer, router])

  // Функция для обработки навигации через Link
  const handleLinkClick = (e: React.MouseEvent<HTMLAnchorElement>, href: string) => {
    const formChanged = JSON.stringify(formData) !== JSON.stringify(initialFormData)
    const dateChanged = 
      (dateRange.from?.getTime() !== initialDateRange.from?.getTime()) ||
      (dateRange.to?.getTime() !== initialDateRange.to?.getTime())
    const coOrganizerChanged = coOrganizer !== initialCoOrganizer
    const hasChanges = formChanged || dateChanged || coOrganizerChanged
    
    if (hasChanges) {
      e.preventDefault()
      e.stopPropagation()
      // Помечаем ссылку как обработанную
      if (e.currentTarget) {
        (e.currentTarget as any).__hasCustomClickHandler = true
      }
      setPendingNavigation(() => () => router.push(href))
      setShowUnsavedDialog(true)
    }
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <Header />
      <AlertDialog open={showUnsavedDialog} onOpenChange={setShowUnsavedDialog}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle>Вы уверены, что хотите уйти?</AlertDialogTitle>
            <AlertDialogDescription>
              Изменения не были сохранены. При выходе они будут потеряны.
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <AlertDialogCancel onClick={handleCancelNavigation}>Отмена</AlertDialogCancel>
            <AlertDialogAction onClick={handleConfirmNavigation}>Да</AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>
      <div className="max-w-[896px] mx-auto px-8">
        {/* Header - Fixed */}
        <div className={cn("sticky top-14 z-20 bg-gray-50 transition-shadow", isScrolled && "shadow-sm")} style={{ height: '72px' }}>
          <div className="flex items-center justify-between gap-4 h-full">
            <div className="flex-1">
              <div className="flex items-start gap-3">
                <Link 
                  href={`/competitions/${competitionId}`} 
                  className="pt-0.5"
                  onClick={(e) => handleLinkClick(e, `/competitions/${competitionId}`)}
                >
                  <Button variant="outline" size="icon" className="h-8 w-8">
                    <ArrowLeft className="h-4 w-4" />
                  </Button>
                </Link>
                <div className="flex-1">
                  <h1 className="text-xl font-semibold text-gray-900">Редактирование соревнования</h1>
                </div>
              </div>
            </div>
            <div className="flex items-center gap-3">
              <Button variant="outline" onClick={handleCancel}>
                Отменить
              </Button>
            <Button 
              onClick={handleSave} 
              disabled={isSaving || !checkUnsavedChanges()} 
              variant="default"
            >
              {isSaving ? "Сохранение..." : "Сохранить"}
            </Button>
            </div>
          </div>
        </div>

        {/* Form Content */}
        <div className="space-y-8 pb-6">
          {/* Основная информация */}
          <div className="bg-white border border-gray-200 rounded-[10px] p-6 shadow-sm">
            <h2 className="text-base font-semibold text-gray-900 mb-5">Основная информация</h2>
            <div className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="fullName" className="text-sm font-medium text-gray-900">
                  Официальное полное наименование
                </Label>
                <Input
                  id="fullName"
                  value={formData.fullName}
                  onChange={(e) => updateField("fullName", e.target.value)}
                  placeholder="Чемпионат России по легкой атлетике 2025"
                  className="text-sm"
                />
              </div>
              <div className="space-y-2">
                <Label htmlFor="shortName" className="text-sm font-medium text-gray-900">
                  Официальное краткое наименование
                </Label>
                <Input
                  id="shortName"
                  value={formData.shortName}
                  onChange={(e) => updateField("shortName", e.target.value)}
                  placeholder="ЧР по легкой атлетике"
                  className="text-sm"
                />
              </div>
            </div>
          </div>

          {/* Организатор */}
          <div className="bg-white border border-gray-200 rounded-[10px] p-6 shadow-sm">
            <h2 className="text-base font-semibold text-gray-900 mb-5">Организатор</h2>
            <div className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="organizerName" className="text-sm font-medium text-gray-900">
                  Полное наименование
                </Label>
                <Input
                  id="organizerName"
                  value={formData.organizerName}
                  onChange={(e) => updateField("organizerName", e.target.value)}
                  placeholder="Федерация легкой атлетики России"
                  className="text-sm"
                />
              </div>
              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="ogrn" className="text-sm font-medium text-gray-900">
                    ОГРН
                  </Label>
                  <Input
                    id="ogrn"
                    value={formData.ogrn}
                    onChange={(e) => updateField("ogrn", e.target.value)}
                    placeholder="1027700232558"
                    className="text-sm"
                  />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="inn" className="text-sm font-medium text-gray-900">
                    ИНН
                  </Label>
                  <Input
                    id="inn"
                    value={formData.inn}
                    onChange={(e) => updateField("inn", e.target.value)}
                    placeholder="7702070139"
                    className="text-sm"
                  />
                </div>
              </div>
            </div>
          </div>

          {/* Кнопка добавления соорганизатора */}
          {!coOrganizer && (
            <Button 
              variant="outline" 
              className="w-full border-dashed h-16"
              onClick={handleAddCoOrganizer}
            >
              Добавить соорганизатора
            </Button>
          )}

          {/* Соорганизатор */}
          {coOrganizer && (
            <div className="bg-white border border-gray-200 rounded-[10px] p-6 shadow-sm">
              <div className="flex items-center justify-between mb-5">
                <h2 className="text-base font-semibold text-gray-900">Соорганизатор</h2>
                <Button
                  variant="ghost"
                  size="icon"
                  onClick={handleRemoveCoOrganizer}
                  className="h-9 w-9 text-gray-500 hover:text-gray-700"
                >
                  <Trash2 className="h-4 w-4" />
                </Button>
              </div>
              <div className="space-y-4">
                <div className="space-y-2">
                  <Label htmlFor="coOrganizerName" className="text-sm font-medium text-gray-900">
                    Полное наименование
                  </Label>
                  <Input
                    id="coOrganizerName"
                    value={coOrganizer.name}
                    onChange={(e) => updateCoOrganizer("name", e.target.value)}
                    placeholder="Наименование организации"
                    className="text-sm"
                  />
                </div>
                <div className="grid grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="coOrganizerOgrn" className="text-sm font-medium text-gray-900">
                      ОГРН
                    </Label>
                    <Input
                      id="coOrganizerOgrn"
                      value={coOrganizer.ogrn}
                      onChange={(e) => updateCoOrganizer("ogrn", e.target.value)}
                      placeholder="1027700232558"
                      className="text-sm"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="coOrganizerInn" className="text-sm font-medium text-gray-900">
                      ИНН
                    </Label>
                    <Input
                      id="coOrganizerInn"
                      value={coOrganizer.inn}
                      onChange={(e) => updateCoOrganizer("inn", e.target.value)}
                      placeholder="7702070139"
                      className="text-sm"
                    />
                  </div>
                </div>
              </div>
            </div>
          )}

          {/* Спортивная информация */}
          <div className="bg-white border border-gray-200 rounded-[10px] p-6 shadow-sm">
            <h2 className="text-base font-semibold text-gray-900 mb-5">Спортивная информация</h2>
            <div className="grid grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label htmlFor="sportType" className="text-sm font-medium text-gray-900">
                  Вид спорта
                </Label>
                <Popover open={sportTypeOpen} onOpenChange={(open) => {
                  setSportTypeOpen(open)
                  if (!open) setSportTypeSearch("")
                }}>
                  <PopoverTrigger asChild>
                    <Button
                      variant="outline"
                      role="combobox"
                      aria-expanded={sportTypeOpen}
                      className="w-full justify-between text-sm font-normal"
                      id="sportType"
                    >
                      {formData.sportType
                        ? sportTypes.find((sport) => sport.value === formData.sportType)?.label
                        : "Выберите вид спорта"}
                      <ChevronDown className="ml-2 h-4 w-4 shrink-0 opacity-50" />
                    </Button>
                  </PopoverTrigger>
                  <PopoverContent className="w-[var(--radix-popover-trigger-width)] p-0 border-0 rounded-xl shadow-lg" align="start">
                    <div className="bg-white rounded-xl border border-gray-200 overflow-hidden">
                      {/* Поиск сверху */}
                      <div className="flex items-center gap-2 px-4 border-b border-gray-100 h-12">
                        <Search className="h-4 w-4 text-gray-400 flex-shrink-0" />
                        <input
                          ref={sportTypeSearchInputRef}
                          type="text"
                          placeholder="Поиск вида спорта..."
                          value={sportTypeSearch}
                          onChange={(e) => setSportTypeSearch(e.target.value)}
                          className="flex-1 text-sm bg-transparent outline-none placeholder:text-gray-400"
                        />
                        {sportTypeSearch && (
                          <button
                            onClick={() => setSportTypeSearch("")}
                            className="p-1 hover:bg-gray-100 rounded transition-colors text-gray-400 hover:text-gray-600"
                          >
                            <X className="h-4 w-4" />
                          </button>
                        )}
                      </div>
                      {/* Список видов спорта */}
                      <div className="max-h-72 overflow-y-auto py-1">
                        {(() => {
                          const filteredSports = sportTypeSearch
                            ? sportTypes.filter((sport) =>
                                sport.label.toLowerCase().includes(sportTypeSearch.toLowerCase())
                              )
                            : sportTypes

                          if (filteredSports.length === 0) {
                            return (
                              <div className="px-4 py-8 text-center text-sm text-gray-400">
                                Ничего не найдено
                              </div>
                            )
                          }

                          return filteredSports.map((sport) => (
                            <button
                              key={sport.value}
                              className="w-full flex items-center gap-3 px-4 py-2.5 text-sm hover:bg-gray-50 transition-colors text-left"
                              onClick={() => {
                                handleSportTypeChange(sport.value)
                                setSportTypeOpen(false)
                                setSportTypeSearch("")
                              }}
                            >
                              <span className="flex-1 text-gray-900">{sport.label}</span>
                              {formData.sportType === sport.value && (
                                <Check className="h-4 w-4 text-orange-500 flex-shrink-0" />
                              )}
                            </button>
                          ))
                        })()}
                      </div>
                    </div>
                  </PopoverContent>
                </Popover>
              </div>
              <div className="space-y-2">
                <Label htmlFor="discipline" className="text-sm font-medium text-gray-900">
                  Спортивная дисциплина
                </Label>
                <Popover open={disciplineOpen} onOpenChange={(open) => {
                  setDisciplineOpen(open)
                  if (!open) setDisciplineSearch("")
                }}>
                  <PopoverTrigger asChild>
                    <Button
                      variant="outline"
                      role="combobox"
                      aria-expanded={disciplineOpen}
                      className="w-full justify-between text-sm font-normal"
                      id="discipline"
                  disabled={!formData.sportType}
                >
                      {formData.discipline
                        ? sportDisciplines[formData.sportType]?.find(
                            (disc) => disc.value === formData.discipline
                          )?.label
                        : formData.sportType
                        ? "Выберите дисциплину"
                        : "Сначала выберите вид спорта"}
                      <ChevronDown className="ml-2 h-4 w-4 shrink-0 opacity-50" />
                    </Button>
                  </PopoverTrigger>
                  <PopoverContent className="w-[var(--radix-popover-trigger-width)] p-0 border-0 rounded-xl shadow-lg" align="start">
                    {formData.sportType && (() => {
                      const disciplines = sportDisciplines[formData.sportType] || []
                      const shouldShowSearch = disciplines.length >= 6
                      
                      return (
                        <div className="bg-white rounded-xl border border-gray-200 overflow-hidden">
                          {/* Поиск сверху - только если дисциплин >= 6 */}
                          {shouldShowSearch && (
                            <div className="flex items-center gap-2 px-4 border-b border-gray-100 h-12">
                              <Search className="h-4 w-4 text-gray-400 flex-shrink-0" />
                              <input
                                ref={disciplineSearchInputRef}
                                type="text"
                                placeholder="Поиск дисциплины..."
                                value={disciplineSearch}
                                onChange={(e) => setDisciplineSearch(e.target.value)}
                                className="flex-1 text-sm bg-transparent outline-none placeholder:text-gray-400"
                              />
                              {disciplineSearch && (
                                <button
                                  onClick={() => setDisciplineSearch("")}
                                  className="p-1 hover:bg-gray-100 rounded transition-colors text-gray-400 hover:text-gray-600"
                                >
                                  <X className="h-4 w-4" />
                                </button>
                              )}
                            </div>
                          )}
                          {/* Список дисциплин */}
                          <div className="max-h-72 overflow-y-auto py-1">
                            {(() => {
                              const filteredDisciplines = shouldShowSearch && disciplineSearch
                                ? disciplines.filter((disc) =>
                                    disc.label.toLowerCase().includes(disciplineSearch.toLowerCase())
                                  )
                                : disciplines

                              if (filteredDisciplines.length === 0) {
                                return (
                                  <div className="px-4 py-8 text-center text-sm text-gray-400">
                                    Ничего не найдено
                                  </div>
                                )
                              }

                              return filteredDisciplines.map((disc) => (
                                <button
                                  key={disc.value}
                                  className="w-full flex items-center gap-3 px-4 py-2.5 text-sm hover:bg-gray-50 transition-colors text-left"
                                  onClick={() => {
                                    updateField("discipline", disc.value)
                                    setDisciplineOpen(false)
                                    setDisciplineSearch("")
                                  }}
                                >
                                  <span className="flex-1 text-gray-900">{disc.label}</span>
                                  {formData.discipline === disc.value && (
                                    <Check className="h-4 w-4 text-orange-500 flex-shrink-0" />
                                  )}
                                </button>
                              ))
                            })()}
                          </div>
                        </div>
                      )
                    })()}
                  </PopoverContent>
                </Popover>
              </div>
            </div>
          </div>

          {/* Параметры проведения */}
          <div className="bg-white border border-gray-200 rounded-[10px] p-6 shadow-sm">
            <h2 className="text-base font-semibold text-gray-900 mb-5">Параметры проведения</h2>
            <div className="space-y-6">
              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label className="text-sm font-medium text-gray-900">
                    Дата начала и завершения
                  </Label>
                  <Popover open={dateRangeOpen} onOpenChange={(open) => {
                    // Разрешаем закрытие только если выбран полный диапазон или пользователь явно закрывает
                    if (!open && dateRange.from && !dateRange.to) {
                      // Если пытаются закрыть без второй даты, предотвращаем закрытие
                      return
                    }
                    setDateRangeOpen(open)
                  }}>
                    <PopoverTrigger asChild>
                      <Button
                        variant="outline"
                        className={cn(
                          "w-full justify-start text-left font-normal text-sm",
                          !dateRange.from && "text-muted-foreground"
                        )}
                      >
                        <CalendarIcon className="mr-2 h-4 w-4" />
                        {dateRange.from ? (
                          dateRange.to ? (
                            <>
                              {formatDate(dateRange.from)} - {formatDate(dateRange.to)}
                            </>
                          ) : (
                            formatDate(dateRange.from)
                          )
                        ) : (
                          <span>Выберите даты</span>
                        )}
                      </Button>
                    </PopoverTrigger>
                    <PopoverContent className="w-auto p-0" align="start" onInteractOutside={(e) => {
                      // Предотвращаем закрытие при клике вне календаря, пока не выбран полный диапазон
                      if (!dateRange.from || !dateRange.to) {
                        e.preventDefault()
                      }
                    }}>
                      <Calendar
                        mode="range"
                        selected={
                          dateRange.from && dateRange.to
                            ? { from: dateRange.from, to: dateRange.to }
                            : dateRange.from
                              ? { from: dateRange.from, to: undefined }
                              : undefined
                        }
                        onSelect={(range) => {
                          setDateRange({ from: range?.from, to: range?.to })
                          // Закрываем модалку только когда выбраны обе даты
                          if (range?.from && range?.to) {
                            setDateRangeOpen(false)
                          }
                        }}
                        numberOfMonths={2}
                      />
                    </PopoverContent>
                  </Popover>
                </div>
                <div className="space-y-2">
                  <Label htmlFor="participantsCount" className="text-sm font-medium text-gray-900">
                    Количество участников
                  </Label>
                  <Input
                    id="participantsCount"
                    type="number"
                    value={formData.participantsCount}
                    onChange={(e) => updateField("participantsCount", e.target.value)}
                    placeholder="150"
                    className="text-sm"
                  />
                </div>
              </div>

              <div className="space-y-3 pt-3">
                <Label className="text-sm font-medium text-gray-900">
                  Включено в Перечень Минспорта России
                </Label>
                <RadioGroup
                  value={formData.inMinistryList}
                  onValueChange={(value) => updateField("inMinistryList", value)}
                  className="flex gap-3"
                >
                  <div className="flex items-center space-x-2">
                    <RadioGroupItem value="yes" id="ministry-yes" />
                    <Label htmlFor="ministry-yes" className="text-sm font-medium text-gray-900 cursor-pointer">
                      Да
                    </Label>
                  </div>
                  <div className="flex items-center space-x-2">
                    <RadioGroupItem value="no" id="ministry-no" />
                    <Label htmlFor="ministry-no" className="text-sm font-medium text-gray-900 cursor-pointer">
                      Нет
                    </Label>
                  </div>
                </RadioGroup>
              </div>

              <div className="space-y-3 pt-3">
                <Label className="text-sm font-medium text-gray-900">Пол участников</Label>
                <RadioGroup
                  value={formData.participantGender}
                  onValueChange={(value) => updateField("participantGender", value)}
                  className="flex flex-col gap-3"
                >
                  <div className="flex items-center space-x-2">
                    <RadioGroupItem value="male" id="gender-male" />
                    <Label htmlFor="gender-male" className="text-sm font-medium text-gray-900 cursor-pointer">
                      Только мужчины
                    </Label>
                  </div>
                  <div className="flex items-center space-x-2">
                    <RadioGroupItem value="female" id="gender-female" />
                    <Label htmlFor="gender-female" className="text-sm font-medium text-gray-900 cursor-pointer">
                      Только женщины
                    </Label>
                  </div>
                  <div className="flex items-center space-x-2">
                    <RadioGroupItem value="both" id="gender-both" />
                    <Label htmlFor="gender-both" className="text-sm font-medium text-gray-900 cursor-pointer">
                      Мужчины и женщины
                    </Label>
                  </div>
                </RadioGroup>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
